import { FlippingCard,FlippingCardBack,FlippingCardFront } from "react-ui-cards"
import "./Book.css"


function Book(props){
    // console.log(props)
    
    return(
        <>
        <FlippingCard className="tune" >
    <FlippingCardBack>
        <div  >
        {props.about}
        </div>
    </FlippingCardBack>
    <FlippingCardFront>
        <div className="Book-cover-page-url" style={{backgroundImage:`url(${props.cover_page_url})`}} >
            
        </div>
    </FlippingCardFront>
</FlippingCard></>
    )
} 

export default Book